      ],
      tData7: []
    }
  },
  computed: {
    entName() {
      return this.viewData.entHealthAssessment.entHealthDetectionRadar.entName || ''
    },
    firstFont() {
      return this.entName && this.entName[0]
    },
    data() {
      return this.viewData
    },
    hasInfo() {
      return this.$root.isScience
    }
  },
  created() {
    this.tableData1[1].col2 = this.viewData.entHealthAssessment.entHealthDialysis.fiveDRader.businessStabilityScore
    this.tableData1[1].col4 = this.viewData.entHealthAssessment.entHealthDialysis.fiveDRader.businessRiskScore
    this.tableData1[1].col6 = this.viewData.entHealthAssessment.entHealthDialysis.fiveDRader.legalRiskScore
    this.tableData1[1].col8 = this.viewData.entHealthAssessment.entHealthDialysis.fiveDRader.intellectualPropertyScore

    let list = this.viewData.entHealthAssessment.entHealthDialysis.fiveDRader.fiveDRaderItemList
    let keys = ['col3', 'col5', 'col7', 'col9']
    for(let i = 0, len = this.tableData1.length; i < len; i++) {
      if(i > 1) {
        keys.forEach(k => {
          let item = list.find(o => o.quotaCode === this.tableData1[i][k])
          this.tableData1[i][k] = item && item.quotaValue
        })
      }
    }

    this.tableData2 = this.viewData.entHealthAssessment.entHealthDialysis.businessStabilityList
    this.tableData3 = this.viewData.entHealthAssessment.entHealthDialysis.businessRiskList
    this.tableData4 = this.viewData.entHealthAssessment.entHealthDialysis.legalRiskList
    this.tableData5 = this.viewData.entHealthAssessment.entHealthDialysis.intellectualPropertyList

    let obj = this.viewData.entHealthDetails.entRegInformation.entBasicInformation
    keys = ['col2', 'col4']
    for(let i = 0, len = this.tableData6.length; i < len; i++) {
      keys.forEach(k => {
        this.tableData6[i][k] = obj[this.tableData6[i][k]]
      })
    }

    this.tableData7 = this.viewData.entHealthDetails.entRegInformation.personnelList
    this.tableData8 = this.viewData.entHealthDetails.entRegInformation.shareholderList
    this.tableData9 = this.viewData.entHealthDetails.entAlterList
    this.tableData10 = this.viewData.entHealthDetails.entAbnormalDetails.caseinfoList
    this.tableData11 = this.viewData.entHealthDetails.entAbnormalDetails.entSharesfrostList
    this.tableData12 = this.viewData.entHealthDetails.entAbnormalDetails.entExceptionList
    this.tableData13 = this.viewData.entHealthDetails.entAbnormalDetails.entLiquidationList
    this.tableData14 = this.viewData.entHealthDetails.litigaCaseList

    if(this.hasInfo) {
      obj = this.viewData.entHealthDetails.patentInformation
      keys = ['col2', 'col4']
      for(let i = 0, len = 6; i < len; i++) {
        keys.forEach(k => {
          if(this.tData1[i][k] && obj[this.tData1[i][k]]) {
            this.tData1[i][k] = obj[this.tData1[i][k]]
          }

          if(i < 5 && this.tData2[i][k] && obj[this.tData2[i][k]]) {
            this.tData2[i][k] = obj[this.tData2[i][k]]
          }
        })
      }

      this.tData3 = this.viewData.entHealthDetails.patentInformation.stdIaPartentList

      this.tData4[0].col2 = this.viewData.entHealthDetails.brandInformation.validBrandNum
      this.tData4[0].col4 = this.viewData.entHealthDetails.brandInformation.validBrandSpeciesDistribution
      this.tData4 = this.tData4.concat(this.viewData.entHealthDetails.brandInformation.validBrandVarietyList)
      this.tData5 = this.viewData.entHealthDetails.brandInformation.stdIaBrandList
      this.tData6[0].col2 = this.viewData.entHealthDetails.copyrightInformation.softwareApplicationNum
      this.tData6[0].col4 = this.viewData.entHealthDetails.copyrightInformation.lastSoftwareApplicationPubYear
      this.tData6[0].col6 = this.viewData.entHealthDetails.copyrightInformation.lastSoftwareApplicationRegYear
      this.tData7 = this.viewData.entHealthDetails.copyrightInformation.stdIaCopyrightList
