export function $rDesc(required = false) {
  required = typeof required === 'object' ? required.required : required
  return [
    { required, message: '', trigger: 'blur' },
    { min: 0, max: 100, message: '100', trigger: 'blur' }
  ]
}
// int
export function $rInt(required) {
  return [
    { required, message: '', trigger: 'blur' },
    {
      validator: (rule, value, callback) => {
        if (required || value) {
          if (intReg.test(value)) {
            callback()
          } else {
            callback(new Error(intMessage))
          }
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}
