//  int long bigDecimal double string date 
const arrAmountRegMap = {
  1: {
    reg: intReg,
    message: intMessage
  },
  2: {
    reg: stringReg,
    message: stringMessage
  },
  5: {
    reg: doubleReg,
    message: doubleMessage
  },
  4: {
    reg: bigDecimalReg,
    message: bigDecimalMessage
  },
  8: {
    reg: longReg,
    message: longMessage
  }
}
export const arrAmountMsg = ',:'
export function $rArrAmountValueType(required, valueType) {
  let isDate = valueType === dateValueTypeMark
  let trigger = isDate ? 'change' : 'blur'
  let rules = [{ required, message: '', trigger }]
  if (!isDate && arrAmountRegMap[valueType]) {
    let { reg, message } = arrAmountRegMap[valueType]
    if (message) {
      rules.push({
        validator: (rule, value, callback) => {
          if (required || value) {
            let arr = value.split(',')
            if (arr.some(a => !reg.test(a))) {
              callback(new Error(arrAmountMsg + message))
            } else {
              callback()
            }
          } else {
            callback()
          }
        },
        trigger
      })
    }
  }
  return rules
}
// natureNum
export function $rNatureNum(required = false) {
  return [
    { required, message: '', trigger: 'blur' },
    { pattern: natureNumReg, message: naturalNumMessage, trigger: 'blur' }
  ]
}
// 
export function $rCount({ min = 0, max, trigger = 'change', label = '' }) {
  return [
    {
      validator: (rule, value, callback) => {
        if (value instanceof Array) {
          value = value.length
        }
        if (value >= min && value <= max) {
          callback()
        } else {
          callback(new Error(`${label} ${min}  ${max} `))
        }
      },
      trigger: trigger
    }
  ]
}
// 
export function $rParticipant() {
  return $rCount({ min: 0, max: 100, trigger: 'change' })
}

// -
// 
// rangeOnly 
export function $rNumRange({
  required = true,
  isInt = false,
  isLong = false,
  isDouble = false,
  isBigDecimal = false,
  rangeOnly = true
}) {
  let reg = null
  let text = null
  let message = null
  if (isInt) {
    reg = intReg
    text = ''
    message = intMessage
  } else if (isLong) {
    reg = longReg
    text = ''
    message = longMessage
  } else if (isDouble) {
    reg = doubleReg
    text = ''
    message = doubleMessage
  } else if (isBigDecimal) {
    reg = bigDecimalReg
    text = ''
    message = bigDecimalMessage
  } else {
    reg = new RegExp('')
    text = ''
    message = ''
  }
  return [
    { required, message: '', trigger: 'blur' },
    {
      validator: (rule, value, callback) => {
        if (required || value) {
          if (!rangeOnly && reg.test(value)) {
            callback()
          } else if (rangeReg.test(value)) {
            let arr = value.split(',')
            if (arr.length === 2) {
              if (value.slice(0, 2) === '[,') {
                return callback(new Error(''))
              }
              if (value.slice(-2) === ',]') {
                return callback(new Error(''))
              }
              let num1 = arr[0].slice(1)
              let num2 = arr[1].slice(0, -1)
              if ((num1 && !reg.test(num1)) || (num2 && !reg.test(num2))) {
                return callback(new Error('' + message))
              }
              if (num1 && num2 && num1 - num2 >= 0) {
                return callback(new Error(''))
              }
              callback()
            } else {
              callback(new Error(''))
            }
          } else {
            if (/^-?\d/.test(value)) {
              callback(new Error(message))
            } else {
              callback(new Error(text + ',/'))
            }
          }
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

// 
export function $rRatio(ratioList, ratioKey) {
  return [
    { required: true, message: '', trigger: 'blur' },
    { pattern: ratioReg, message: ratioMessage, trigger: 'blur' },
    {
      validator: (rule, value, callback) => {
        let sum = 0
        ratioList.forEach(l => {
          sum += l[ratioKey] * 1 || 0
        })
        if (ratioList.every(p => p[ratioKey])) {
          if (sum !== 100) {
            callback(new Error('100'))
          } else {
            callback()
          }
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

// excel 
export function $rExcel(required, file, size = 2) {
  return $rFileValidate(excelFileReg, 'xls/xlsx', size, required, '', file)
}

