/**
 * 
 *  selectOperate: {
 *   majorKey: ,
 *   params: {
 *     ...
 *   }
 * }
 *  loadSelectDialogTableData 
 * table ref   selectTable
 *
 */
export default {
  data() {
    return {
      selectOperate: {
        // majorKey:   id
        majorKey: 'id',
        currentCheckedRows: [],
        tableData: []
      }
    }
  },
  methods: {
    m_selectChange(rows, row) {
      console.log(rows, row)
      if (rows.indexOf(row) > -1) {
        this.selectOperate.currentCheckedRows.push(row)
      } else {
        this.removeRowSelected(row)
      }
    },
    m_rowClick(row) {
      // const selected = this.selectOperate.currentCheckedRows.indexOf(row) > -1
      // this.$refs.selectTable.toggleRowSelection(row)
      // if (selected) {
      //   this.removeRowSelected(row)
      // } else {
      //   this.selectOperate.currentCheckedRows.push(row)
      // }
    },
    removeRowSelected(row) {
      const index = this.selectOperate.currentCheckedRows.findIndex(r => r[this.selectOperate.majorKey] === row[this.selectOperate.majorKey])
      this.selectOperate.currentCheckedRows.splice(index, 1)
    },
    m_selectAll(rows) {
      console.log(rows)
      this.selectOperate.tableData.forEach(r => {
        const index = this.selectOperate.currentCheckedRows.findIndex(row => row[this.selectOperate.majorKey] === r[this.selectOperate.majorKey])
        if (rows.length && index === -1) {
          this.selectOperate.currentCheckedRows.push(r)
        }
        if (!rows.length && index > -1) {
          this.selectOperate.currentCheckedRows.splice(index, 1)
        }
      })
    },
    m_validateSelect() {
      if (this.selectOperate.currentCheckedRows.length) {
        return true
        // this.$downloadFileByIframe('/sign/contract/download?' + fileIds)
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        })
      }
    }
  }
}



