<template>
  <div>
    <div class="current-router-title">{{menuTitle}}
      <div v-if="$root.isAreaUser"
           class="current-router-operate">
        <el-button type="primary"
                   icon="el-icon-user"
                   @click="allot"></el-button>
        <el-button icon="el-icon-bottom-left"
                   @click="importFile"></el-button>
        <el-button icon="el-icon-top-right"
                   @click="exportFile"></el-button>
        <el-button icon="el-icon-download"
                   @click="downloadTemplate"></el-button>
      </div>
    </div>
    <div class="content-view"
         v-loading="table.loading">
      <div class="table-query-container">
        <div class="table-query">
          <div class="table-query-item">
            <div class="table-query-label">:</div>
            <el-select-mine v-model="params.verification"
                            :options="clueAuditOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">:</div>
            <el-date-picker v-model="params.informTimeStart"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">:</div>
            <el-input v-model="params.informName"
                      class="table-query-content"
                      placeholder=""
                      clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">:</div>
            <el-select-mine v-model="params.overdue"
                            :options="overdueOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div v-if="$root.isStreetUser"
               class="table-query-item">
            <div class="table-query-label">:</div>
            <el-date-picker v-model="params.informTimeEnd"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">:</div>
            <el-select-mine v-model="params.rewardStatus"
                            :options="awardSituationOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">:</div>
            <el-select-mine v-model="params.checkStatus"
                            :options="inspectStatusOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">{{$root.isAreaUser?'':''}}:</div>
            <el-cascader v-model="params.areaId"
                         :props="areaProps"
                         class="table-query-content"
                         filterable
                         clearable />
          </div>
          <div v-if="$root.isAreaUser"
               class="table-query-item">
            <div class="table-query-label">:</div>
            <el-select-mine v-model="params.assignment"
                            :options="allotStatusOptions"
                            class="table-query-content"
                            clearable />
          </div>
        </div>
        <div class="table-query-btn">
          <el-button type="primary"
                     @click="m_queryTable"></el-button>
          <el-button type="primary"
                     @click="m_resetTable"></el-button>
        </div>
      </div>
      <div class="table-container">
        <div>
          <el-table :data="table.data"
                    stripe
                    border
                    style="width: 100%"
                    :row-key="selectOperate.majorKey"
                    @select="m_selectChange"
                    @select-all="m_selectAll"
                    @row-click="m_rowClick">
            <el-table-column v-if="$root.isAreaUser"
                             type="selection"
                             width="40"
                             fixed="left">
            </el-table-column>
            <el-table-column type="index"
                             width="50"
                             label=""
                             fixed="left">
            </el-table-column>
            <el-table-column prop="clueNumber"
                             label=""
                             width="150"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="informName"
                             label=""
                             min-width="150"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="areaName"
                             :label="$root.isAreaUser?'':''"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="informTime"
                             label=""
                             width="180">
            </el-table-column>
            <el-table-column v-if="$root.isAreaUser"
                             label=""
                             width="100">
              <template slot-scope="scope">{{allotStatusOptionsObject[scope.row.assignment]}}</template>
            </el-table-column>
            <el-table-column label=""
                             width="100">
              <template slot-scope="scope">{{inspectStatusOptionsObject[scope.row.checkStatus]}}</template>
            </el-table-column>
            <el-table-column label=""
                             width="100">
              <template slot-scope="scope">{{overdueOptionsObject[scope.row.overdue]}}</template>
            </el-table-column>
            <el-table-column label=""
                             width="100">
              <template slot-scope="scope">{{clueAuditOptionsObject[scope.row.verification]}}</template>
            </el-table-column>
            <el-table-column label=""
                             width="100">
              <template slot-scope="scope">{{awardSituationOptionsObject[scope.row.rewardStatus]}}</template>
            </el-table-column>
            <el-table-column width="170"
                             label=""
                             fixed="right">
              <template slot-scope="scope">
                <el-button type="text"
                           @click="viewRow(scope.row)"
                           title=""
                           class="icon_button"><i class="el-icon-view"></i></el-button>
                <template v-if="$root.isAreaUser">
                  <template v-if="scope.row.assignment===allotAssigned">
                    <el-button v-if="scope.row.checkStatus===inspectIs"
                               type="text"
                               @click="reInspectRow(scope.row)"
                               title=""
                               class="icon_button"><i class="el-icon-search"></i></el-button>
                    <el-button v-else
                               type="text"
                               @click="cancelRow(scope.row)"
                               title=""
                               class="icon_button"><i class="el-icon-refresh-left"></i></el-button>
                  </template>
                  <template v-else>
                    <el-button type="text"
                               @click="allotRow(scope.row)"
                               title=""
                               class="icon_button"><i class="el-icon-s-custom"></i></el-button>
                    <el-button type="text"
                               @click="deleteRow(scope.row)"
                               title=""
                               class="icon_button"><i class="el-icon-delete"></i></el-button>
                  </template>
                  <el-button v-if="scope.row.refundCount>0"
                             type="text"
                             @click="viewReturnRecord(scope.row)"
                             title=""
                             class="icon_button"><i class="el-icon-document"></i></el-button>
                </template>
                <template v-else>
                  <el-button v-if="scope.row.checkStatus!==inspectIs"
                             type="text"
                             @click="returnRow(scope.row)"
                             title=""
                             class="icon_button"><i class="el-icon-refresh-left"></i></el-button>
                  <el-button type="text"
                             @click="inspectRow(scope.row)"
                             title=""
                             class="icon_button"><i class="el-icon-search"></i></el-button>
                </template>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <el-pagination :current-page.sync="params.pageNo"
                     :page-size.sync="params.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :page-sizes="pageSizes"
                     :total="table.total"
                     @size-change="m_handleSizeChange"
                     @current-change="m_handleCurrentChange" />
    </div>
    <component :is="currentOperateView"
               :viewData="viewData"
               @close="currentOperateView=''"
               @reload="m_tableLoadData" />
  </div>
</template>
<script>
import tableQuery from '@/mixins/tableQuery'
import tableSelect from '@/mixins/tableSelect'
import ReportComplainImport from './ReportComplainImport'
import ReportComplainAllot from './ReportComplainAllot'
import ReportComplainInspect from './ReportComplainInspect'
import ReportComplainView from './ReportComplainView'
import ReportComplainReturnRecord from './ReportComplainReturnRecord'
import * as CONSTANTS from '@/constants/common'
export default {
  name: 'ReportComplain',
  mixins: [tableQuery, tableSelect],
  props: {
    menuTitle: {
      type: String,
      required: true
    }
  },
  data() {
    let that = this
    return {
      params: {
        verification: '',
        informTimeStart: '',
        informTimeEnd: '',
        informName: '',
        overdue: '',
        checkStatus: '',
        rewardStatus: '',
        areaId: '',
        assignment: ''
      },
      tableUrl: '/informs/list',
      viewData: null,

      currentOperateView: '',

      areaProps: {
        checkStrictly: true,
        emitPath: false,
        lazy: true,
        label: 'areaName',
        value: 'id',
        lazyLoad (node, resolve) {
          let url = node.root ? '' : ('/' + node.value)
          if(node.isLeaf) {
            resolve([])
          }else{
            that.$http.get('/areas/subs' + url, '', {customMessage: true}).then(res => {
              if(res.code === that.$root.codeSuccess) {
                res.content.forEach(a => {
                  a.leaf = a.type === that.$root.areaLeaf
                })
                resolve(res.content)
              }else{
                resolve([])
              }
            })
          }
        }
      },

      clueAuditOptions: CONSTANTS.CLUE_AUDIT,
      allotStatusOptions: CONSTANTS.ALLOT_STATUS,
      inspectStatusOptions: CONSTANTS.INSPECT_STATUS,
      overdueOptions: CONSTANTS.OVERDUE,
      awardSituationOptions: CONSTANTS.AWARD_SITUATION,

      allotAssigned: CONSTANTS.ALLOT_ASSIGNED,
      inspectIs: CONSTANTS.INSPECT_IS
    }
  },
  computed: {
    clueAuditOptionsObject() {
      return this.$optionsToObject(this.clueAuditOptions)
    },
    allotStatusOptionsObject() {
      return this.$optionsToObject(this.allotStatusOptions)
    },
    inspectStatusOptionsObject() {
      return this.$optionsToObject(this.inspectStatusOptions)
    },
    overdueOptionsObject() {
      return this.$optionsToObject(this.overdueOptions)
    },
    awardSituationOptionsObject() {
      return this.$optionsToObject(this.awardSituationOptions)
    },
    returnRowTitle() {
      return this.$root.isAreaUser ? '' : ''
    }
  },
  methods: {
    allot() {
      let rows = this.selectOperate.currentCheckedRows.filter(row => row.assignment !== CONSTANTS.ALLOT_ASSIGNED)
      let len = rows.length
      if(len) {
        let h = this.$createElement
        let t = 20 * len
        let t1 = Math.floor(t / 60)
        let t2 = t % 60
        let customContent = [
          h('div', {}, [
            h('div', {
              class: 'text-center'
            }, '' + len + ''),
            h('div', {
              class: 'text-center',
              style: {
                marginTop: '10px'
              }
            }, [
              h('span', {}, ' : '),
              h('span', {
                style: {
                  color: '#0082FF'
                }
              }, `${t1}${t2}`),
              h('span', {}, ' , ')
            ])
          ])
        ]

        this.$confirmTags({
          title: '',
          showCancelButton: false,
          confirmButtonText: '',
          customContent,
          beforeClose: (action, instance, done) => {
            if(action === 'confirm') {
              instance.$refs.confirm.loading = true
              this.$http.put('/informs/dispatcher/batch', rows.map(r => r.id)).then(res => {
                instance.$refs.confirm.loading = false
                if(res.code === this.$root.codeSuccess) {
                  done()
                  let failNum = res.content.length
                  this.$message.success(`${len} ${len - failNum}   ${failNum} `)
                  this.m_tableLoadData()
                }
              })
            }else{
              done()
            }
          }
        })
      }else{
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        })
      }
    },
    importFile(){
      this.currentOperateView = 'ReportComplainImport'
    },
    exportFile() {
      // if(this.m_validateSelect()) {
      this.$downloadFileByIframe('/informs/export' + this.$paramsSerializer(this.params))
      // }
    },
    downloadTemplate() {
      this.$downloadFileByIframe('static/template/inform_template.xlsx', '')
    },
    viewRow(row) {
      this.currentOperateView = 'ReportComplainView'
      this.viewData = row
    },
    inspectRow(row) {
      this.currentOperateView = 'ReportComplainInspect'
      this.viewData = row
    },
    reInspectRow(row) {
      this.$http.put('/informs/recheck/' + row.id).then(res => {
        if(res.code === this.$root.codeSuccess) {
          this.$message.success('')
          this.m_tableLoadData()
        }
      })
    },
    cancelRow(row) {
      this.$confirm('', '', {
        beforeClose: (action, instance, done) => {
          if(action === 'confirm') {
            instance.$refs.confirm.loading = true
            this.$http.put('/informs/revoke/' + row.id, '').then(res => {
              instance.$refs.confirm.loading = false
              if(res.code === this.$root.codeSuccess) {
                done()
                this.$message.success('')
                this.m_tableLoadData()
              }
            })
          }else{
            done()
          }
        }
      })
    },
    returnRow(row) {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /[^\s]/,
        inputErrorMessage: '',
        beforeClose: (action, instance, done) => {
          console.log(instance.inputValue)
          if(action === 'confirm') {
            instance.$refs.confirm.loading = true
            this.$http.put('/informs/return/' + row.id, {refundReason: instance.inputValue}).then(res => {
              instance.$refs.confirm.loading = false
              if(res.code === this.$root.codeSuccess) {
                done()
                this.$message.success('')
                this.m_tableLoadData()
              }
            })
          }else{
            done()
          }
        }
      })
    },
    allotRow(row){
      this.viewData = row
      this.currentOperateView = 'ReportComplainAllot'
    },
    deleteRow(row){
      this.$confirm('', '').then(() => {
        this.$http.delete('/informs/del/' + row.id, '').then(res => {
          if(res.code === this.$root.codeSuccess) {
            this.$message.success('')
            this.m_tableLoadData()
          }
        })
      })
    },
    viewReturnRecord(row) {
      this.viewData = row
      this.currentOperateView = 'ReportComplainReturnRecord'
    }
  },
  beforeDestroy() {},
  components: {
    ReportComplainImport,
    ReportComplainAllot,
    ReportComplainInspect,
    ReportComplainView,
    ReportComplainReturnRecord
  }
}
</script>




