<template>
  <div class="container-absolute border-none"
       v-loading="loading">
    <div class="overflow-auto">
      <div class="padding-20 border background-white">
        <div class="row-name-title">
          <div class="first-font">{{firstFont}}</div>
          <div class="row-title-content">
            <div class="row-title-top">
              <div class="name-title">{{form.taskEnterprise_enterpriseName}}</div>
              <div class="row-name-right">
                <el-button type="primary"
                           @click="$emit('close')"></el-button>
              </div>
            </div>
            <div class="row-title-form">
              <div v-for="item in formData"
                   :key="item.value"
                   class="row-form-item">
                <div class="row-form-label">{{item.label}} :</div>
                <div class="row-form-content">{{form[item.value]}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bound-bar"></div>
      <div class="border  background-white">
        <div class="sub-title"></div>
        <div class="blur-bar-title"></div>
        <el-table :show-header="false"
                  :data="tableData1"
                  :span-method="spanMethod1"
                  class="form-view-table common-detail-table"
                  border>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label1}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value1]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label2}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value2]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label3}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value3]}}
            </template>
          </el-table-column>
        </el-table>
        <div class="blur-bar-title"></div>
        <el-table :show-header="false"
                  :data="tableData2"
                  :span-method="spanMethod2"
                  class="form-view-table common-detail-table"
                  border>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label1}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value1]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label2}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value2]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label3}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value3]}}
            </template>
          </el-table-column>
        </el-table>
        <div class="blur-bar-title"></div>
        <el-table :data="checkProgress"
                  stripe
                  border>
          <el-table-column type="index"
                           width="50"
                           label="">
          </el-table-column>
          <el-table-column width="150"
                           label="">
            <template slot-scope="scope">{{showCheckProcessTypeName(scope.row.processType)}}</template>
          </el-table-column>
          <el-table-column width="180"
                           label="">
            <template slot-scope="scope">{{scope.row.createTime}}</template>
          </el-table-column>
          <el-table-column label=""
                           show-overflow-tooltip>
            <template slot-scope="scope">{{scope.row.processMessage}}</template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
import * as CONSTANTS from '@/constants/common'
export default {
  name: 'CheckDisposeView',
  props: {
    viewData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      form: {},
      formData: [
        {
          label: '',
          value: 'task_taskName'
        },
        {
          label: '',
          value: 'task_dueTime'
        },
        {
          label: '',
          value: 'task_taskNumber'
        },
        {
          label: '',
          value: 'task_startTime'
        },
        {
          label: '',
          value: 'task_taskSource'
        },
        {
          label: '',
          value: 'taskCheck_checkStatus_text'
        }
      ],
      tableData1: [
        {
          label1: '',
          value1: 'taskEnterprise_enterpriseName',
          label2: '',
          value2: 'taskEnterprise_establishedTime',
          label3: '',
          value3: 'taskEnterprise_managementStatus'
        },
        {
          label1: '',
          value1: 'taskEnterprise_legalPerson',
          label2: '()',
          value2: 'taskEnterprise_registrationAmount',
          label3: '',
          value3: 'taskEnterprise_registrationAddress'
        },
        {
          label1: '',
          value1: 'taskEnterprise_registrationNumber',
          label2: '',
          value2: 'taskEnterprise_disposalStage_text',
          label3: '',
          value3: 'taskEnterprise_judicialCase'
        },
        {
          label1: '',
          value1: 'taskEnterprise_caseTime',
          label2: '',
          value2: 'taskEnterprise_industry',
          label3: '',
          value3: 'taskEnterprise_businessAddress'
        },
        {
          label1: '',
          value1: 'taskEnterprise_punishment',
          label3: '',
          value3: 'taskEnterprise_riskLevel_text'
        },
        {
          label1: '',
          value1: 'taskCheck_relatedClues'
        }
      ],
      tableData2: [
        {
          label1: '',
          value1: 'taskDisposition_involvePeople',
          label2: '()',
          value2: 'taskDisposition_money_text',
          label3: '',
          value3: 'taskDisposition_employeesNumber'
        },
        {
          label1: '',
          value1: 'taskDisposition_fundRaisingMode_text',
          label2: '',
          value2: 'taskDisposition_outstandingFund',
          label3: '',
          value3: 'taskDisposition_withdrawalFund'
        },
        {
          label1: '',
          value1: 'taskDisposition_involveInternet_text',
          label2: '',
          value2: 'taskDisposition_regionalScale_text',
          label3: '',
          value3: 'taskDisposition_involveArea'
        },
        {
          label1: '',
          value1: 'taskDisposition_riskDetails'
        },
        {
          label1: '',
          value1: 'taskDisposition_done_text'
        }
      ],

      checkProgress: []
    }
  },
  computed: {
    firstFont() {
      return this.form.taskEnterprise_enterpriseName && this.form.taskEnterprise_enterpriseName[0]
    },
    inspectStatusOptionsObject() {
      return this.$optionsToObject(CONSTANTS.INSPECT_STATUS)
    },
    disposeStageOptionsObject() {
      return this.$optionsToObject(CONSTANTS.DISPOSE_STAGE)
    },
    riskLevelOptionsObject() {
      return this.$optionsToObject(CONSTANTS.RISK_LEVEL)
    },
    collectModelOptionsObject() {
      return this.$optionsToObject(CONSTANTS.COLLECT_MODEL)
    },
    modelOptionsObject() {
      return this.$optionsToObject(CONSTANTS.MODEL)
    },
    areaTypeOptionsObject() {
      return this.$optionsToObject(CONSTANTS.AREA_TYPE)
    }
  },
  created() {
    this.getDetailData()
    this.getCheckProgress()
  },
  mounted() {},
  methods: {
    getDetailData() {
      this.loading = true
      this.$http.get('/verifies/view/' + this.viewData.id).then(res => {
        if(res.code === this.$root.codeSuccess) {
          let obj = {}
          for(let key in res.content) {
            if(typeof res.content[key] === 'object') {
              for(let k in res.content[key]) {
                obj[key + '_' + k] = res.content[key][k]
              }
            }else{
              obj[key] = res.content[key][key]
            }
          }
          obj.taskCheck_checkStatus_text = this.inspectStatusOptionsObject[obj.taskCheck_checkStatus]
          obj.taskEnterprise_disposalStage_text = this.disposeStageOptionsObject[obj.taskEnterprise_disposalStage]
          obj.taskEnterprise_riskLevel_text = this.riskLevelOptionsObject[obj.taskEnterprise_riskLevel]
          obj.taskDisposition_money_text = obj.taskDisposition_money * 0.000001
          obj.taskDisposition_fundRaisingMode_text = this.collectModelOptionsObject[obj.taskDisposition_fundRaisingMode]
          obj.taskDisposition_involveInternet_text = this.modelOptionsObject[obj.taskDisposition_involveInternet]
          obj.taskDisposition_regionalScale_text = this.areaTypeOptionsObject[obj.taskDisposition_regionalScale]
          obj.taskDisposition_done_text = ''
          CONSTANTS.SOLVE_MODEL.forEach(m => {
            if(res.content.taskDisposition[m.value]) {
              obj.taskDisposition_done_text += `${m.label} ${res.content.taskDisposition[m.value]} `
            }
          })
          obj.taskDisposition_done_text = obj.taskDisposition_done_text.slice(0, -1)

          this.form = obj
        }else{
          this.cancel()
        }
        this.loading = false
      })
    },
    getCheckProgress () {
      this.loading = true
      this.$http.get('/verifies/process/list/' + this.viewData.id).then(res => {
        if(res.code === this.$root.codeSuccess) {
          console.log('', res.content)
          this.checkProgress = res.content
        }else{
          this.cancel()
        }
        this.loading = false
      })
    },
    showCheckProcessTypeName (name) {
      if (name) {
        switch (name) {
          case 'DISPATCHER':
            return ''
          case 'RETURN':
            return ''
          case 'PROCESS':
            return ''
          case 'RECHECK':
            return ''
          case 'REVOKE':
            return ''
        }
      }
    },
    spanMethod1({rowIndex, columnIndex}) {
      if(rowIndex === 4) {
        if(columnIndex === 1) {
          return {
            rowspan: 1,
            colspan: 3
          }
        }else if(columnIndex > 1 && columnIndex < 4) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }else if(rowIndex === 5) {
        if(columnIndex === 1) {
          return {
            rowspan: 1,
            colspan: 5
          }
        }else if(columnIndex > 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    spanMethod2({rowIndex, columnIndex}) {
      if(rowIndex > 2) {
        if(columnIndex === 1) {
          return {
            rowspan: 1,
            colspan: 5
          }
        }else if(columnIndex > 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    cancel() {
      this.$emit('close')
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>
<style lang="scss" scoped>
.overflow-auto {
  min-height: 100%;
}
</style>



